import { HDRTextureInfo } from "../../../RenderEngine/HDRTextureInfo";
import { RenderCapable } from "../../../RenderEngine/RenderEnum/RenderCapable";
import { TextureFormat } from "../../../RenderEngine/RenderEnum/TextureFormat";
import { WrapMode } from "../../../RenderEngine/RenderEnum/WrapMode";
import { LayaGL } from "../../../layagl/LayaGL";
import { Texture2D } from "../../../resource/Texture2D";
import { Base64Tool } from "../../../utils/Base64Tool";


const dfg_128 = ""

const RGBDMaxRange = 255;
/**
 * @internal
 */
export class PBRDefaultDFG {

    // todo 
    private static _defaultDFG: Texture2D;
    public static get defaultDFG(): Texture2D {
        return PBRDefaultDFG._defaultDFG;
    }
    public static set defaultDFG(value: Texture2D) {
        PBRDefaultDFG._defaultDFG = value;
    }

    static DefaultDfgTexture() {
        let hdrBuffer = Base64Tool.decode(dfg_128);
        let hdrInfo = HDRTextureInfo.getHDRInfo(hdrBuffer);

        let supportFloatTex = LayaGL.renderEngine.getCapable(RenderCapable.TextureFormat_R32G32B32A32);
        let supportFloatLinearFiltering = LayaGL.renderEngine.getCapable(RenderCapable.Texture_FloatLinearFiltering);

        let tex: Texture2D;

        if (supportFloatTex && supportFloatLinearFiltering) {
            tex = new Texture2D(hdrInfo.width, hdrInfo.height, hdrInfo.format, false, false, false);
            tex.setHDRData(hdrInfo);
        }
        else {
            tex = new Texture2D(hdrInfo.width, hdrInfo.height, TextureFormat.R8G8B8A8, false, false, false);

            let pixelCount = hdrInfo.width * hdrInfo.height;
            let hdrPixelData = <Float32Array>hdrInfo.readScanLine();
            let rgbdPixelData = new Uint8Array(pixelCount * 4);

            for (let index = 0; index < pixelCount; index++) {

                let r = hdrPixelData[index * 4];
                let g = hdrPixelData[index * 4 + 1];
                let b = hdrPixelData[index * 4 + 2];

                let maxRGB = Math.max(r, g, b, 1e-5);
                let d = Math.max(1.0, RGBDMaxRange / maxRGB);
                d = Math.min(1, Math.max(0, (d / 255)));

                rgbdPixelData[index * 4] = Math.min(1, Math.max(0, (r * d))) * 255;
                rgbdPixelData[index * 4 + 1] = Math.min(1, Math.max(0, (g * d))) * 255;
                rgbdPixelData[index * 4 + 2] = Math.min(1, Math.max(0, (b * d))) * 255;
                rgbdPixelData[index * 4 + 3] = d * 255;
            }

            tex.setPixelsData(rgbdPixelData, false, false);
        }

        tex.lock = true;
        tex.wrapModeU = WrapMode.Clamp;
        tex.wrapModeV = WrapMode.Clamp;
        tex.anisoLevel = 1;

        this._defaultDFG = tex;
    }

}